@font-face {
    font-family: sitka;
    src: url('../Fonts/SitkaVF.ttf');
}

.footerbg{
    background-image: url('../../img/Recurso\ 50.png');
    
    background-size: contain;
    height: 700px;
    background-repeat: no-repeat;
    margin-top: -190px;
    background-position-x: center;
}

.mbi{
    color: #537F6a;
    margin-right: 20px;
    
}

.nametitle{
    margin-top: 180px;
    color: #537F6A;
    font-family: sitka;
    font-weight: bold;
    font-size: 60px;
    font-weight: 500;
}

.h3info{
    color: #537F6A;
    margin-top: 20px;
    font-family: sitka;
    font-size: 33px;
}

.about{
    margin-top: 170px;
    margin-left: 398px;
}

.imgfooter{
    width: 22%;
    margin-right: 25px;
    margin-top: 10px;
}
.right{
    margin-top: 170px;
    padding-bottom: 20px;
    margin-left: 190px;
    
}
@media only screen and (max-width: 450px) {
    .footerbg{
        background-image: url('../../img/Fondo_banner1.png');
        height: 200px;
    }
    .nametitle{
        margin-top: 40px;
        margin-left: 30px;

    }
    .mbi{
        margin-left: 50px;
        color:#537F6A;
        margin-right: 5px;
    }
    .h3info{
        margin-top: 30px;
        font-size: 15px;
        width: 100%;
    }
    .todo{
        flex-direction: column;
    }
    .about{
        margin-top: 20px;
        margin-left: 50px;
    }
    .imgfooter{
        margin-left: 34px;
        width: 10%;
        margin-bottom: 30px;
    }
    .right{
        margin-top: 30px;
        margin-left: 100px;
        width: 60%;
    }
  
  }

